import React, { useRef, useEffect, useState } from 'react';
import './App.css';
import { Layout, Menu } from 'antd';
import * as THREE from 'three';

const { Content, Sider } = Layout;

const ThreeJSScene = ({ updateObjects }) => {
  const sceneContainerRef = useRef(null); //   DOM-
  const sceneRef = useRef(null); //    Three.js
  const [initialized, setInitialized] = useState(false); //   

  useEffect(() => {
    if (initialized) {
      
      return;
    } else {

      console.log(' ')
      //   Three.js
      const scene = new THREE.Scene(); //  
      scene.background = new THREE.Color(0x8DADB2);

      const camera = new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 0.1, 1000); //  
      const renderer = new THREE.WebGLRenderer(); //  

      renderer.setSize(window.innerWidth, window.innerHeight); //   
      sceneContainerRef.current.appendChild(renderer.domElement); //    DOM-

      camera.position.z = 5; //    

      //    scene, camera  renderer  ref   
      sceneRef.current = { scene, camera, renderer };

      const animate = function () {
        requestAnimationFrame(animate); //    
        renderer.render(scene, camera); //     
      };

      animate(); //  
      setInitialized(true); //  ,   
    }


  }, [initialized]);

  console.log(sceneContainerRef, ' ')


  useEffect(() => {
    
    if (initialized && sceneRef.current) {
      //    ,   
      console.log(` `);
      updateObjects(sceneRef.current.scene);
    }
  }, [initialized, updateObjects]);

  return (
    <div className='threejs_scene' ref={sceneContainerRef} style={{ width: '80%', height: '100vh' }} />
  );
};

const App = () => {
  const [selectedObject, setSelectedObject] = useState('1'); //   

  //      
  const updateObjects = (scene) => {
    //      
    while (scene.children.length > 0) {
      scene.remove(scene.children[0]);
    }

    //          
    if (selectedObject === '1') {
      const geometry = new THREE.BoxGeometry();
      const material = new THREE.MeshBasicMaterial({ color: 0xCBC0AD });
      const cube = new THREE.Mesh(geometry, material);
      scene.add(cube);
    } else if (selectedObject === '2') {
      const geometry = new THREE.SphereGeometry();
      const material = new THREE.MeshBasicMaterial({ color: 0x361D2E });
      const sphere = new THREE.Mesh(geometry, material);
      scene.add(sphere);
    }
  };

  const items = [
    { key: '1', label: 'Option 1' },
    { key: '2', label: 'Option 2' },
    { key: '3', label: 'Option 3' },
  ];

  return (
    <Layout style={{ height: '100vh' }}>
      <Sider>
        <Menu
          mode="inline"
          onClick={(e) => setSelectedObject(e.key)} //      
          defaultSelectedKeys={['1']}
          theme="dark"
          items={items}
        />
      </Sider>
      <Layout>
        <Content>
          <ThreeJSScene updateObjects={updateObjects} />
        </Content>
      </Layout>
    </Layout>
  );
};

export default App;
